.sort-block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  justify-content: flex-end;
  position: absolute;
  left: 165px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
}

.sort-block__toggler {
  display: inline-block;
  background: transparent;
  cursor: pointer;
  color: #fff;
  font-size: 14px;
  line-height: 1;
  position: relative;
}

.sort-block__toggler:hover .sort-block__result {
  color: #d9d9d9;
}

.sort-block__toggler:hover:after {
  border-top: 8px solid #d9d9d9;
}

.sort-block__toggler:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 8px solid white;
  position: absolute;
  top: 50%;
  transform: translateY(-25%);
  right: -15px;
}

.sort-block__result {
  color: #fff;
}

.sort-block__dropdown-list {
  display: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: calc(100% + 11px);
  left: -1px;
  right: 0;
  min-width: 200px;
  width: calc(100% + 12px);
  background: #fff;
  color: #000000;
  border: 1px solid #efefef;
  border-radius: 4px;
  z-index: 1000;
  text-align: center;
  box-shadow: 3px 3px 5px 0 #757475;
}

.sort-block__dropdown-list li {
  padding: 10px;
}

.sort-block__dropdown-list li:hover {
  background: #f2f2f2;
}

