/*геолокация и все что с ней связано для мобильных устройств*/
.dc-checkbox__pseudo-checkbox {
  display: block;
  height: 20px;
  width: 20px;
  background: #fff;
  border: 1px solid #efefef;
  border-radius: 4px;
  position: relative;
  margin-right: 10px;
}

.dc-checkbox__pseudo-checkbox:before {
  content: "";
  display: block;
  position: absolute;
  bottom: 5px;
  left: 3px;
  width: 0;
  height: 2px;
  border-radius: 1px;
  background: #ce7c72;
  transform: rotate(45deg);
}

.dc-checkbox__pseudo-checkbox:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 8px;
  left: 5px;
  width: 0;
  height: 2px;
  border-radius: 1px;
  background: #ce7c72;
  transform: rotate(120deg);
}

.dc-checkbox__hidden-input {
  display: none;
}

.dc-checkbox__hidden-input + label {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-end;
  cursor: pointer;
  margin: 0 0 10px 0;
}

.dc-checkbox__hidden-input + label:hover {
  color: #ce7c72;
}

.dc-checkbox__hidden-input + label:hover .dc-checkbox__pseudo-checkbox {
  border-color: #c9c9c9;
}

.dc-checkbox__hidden-input:checked + label {
  color: #ce7c72;
}

.dc-checkbox__hidden-input:checked + label .dc-checkbox__pseudo-checkbox:before {
  width: 8px;
}

.dc-checkbox__hidden-input:checked + label .dc-checkbox__pseudo-checkbox:after {
  width: 14px;
}

.dc-input-block {
  margin: 0;
}

.dc-input-block__label {
  display: block;
  margin: 0 0 15px 0;
  height: 65px;
  position: relative;
}

.dc-input-block__label:hover input {
  border-color: #c9c9c9;
}

.dc-input-block__label_select:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 8px solid white;
  border-top: 8px solid black;
  position: absolute;
  top: 50%;
  transform: translateY(-10px);
  right: 10px;
}

.dc-input-block__input {
