  line-height: 20px;
  width: 100%;
}

.dc-input-block__input:focus {
  border-color: #c9c9c9;
  outline: 0;
}

.dc-input-block__input:focus + .dc-input-block__placeholder {
  transform: matrix(1, 0, 0, 1, 0, -48);
}

.dc-input-block__input.value-exists + .dc-input-block__placeholder {
  transform: matrix(1, 0, 0, 1, 0, -48);
}

.dc-input-block__placeholder {
  display: inline-block;
  font-size: 16px;
  color: gray;
  padding-left: 10px;
  transform: matrix(1, 0, 0, 1, 0, -38);
  cursor: text;
