  color: #9e9e9e;
}

.filters__toggler:focus, .filters-mobile__button:focus {
  color: #9e9e9e;
  text-decoration: none;
}

.filters__toggler.expanded, .expanded.filters-mobile__button {
  border-radius: 4px 4px 0 0;
